import React from 'react';


class ListItem extends React.Component {


    //removing the tasks.
    onRemove = () => {
        this.props.handleDelete();
    }


    render() {
        const { item } = this.props;
        return (
            <li> {item}
                <div className="row">
                    <i class="fa fa-trash-o"
                        title="Delete"
                        onClick={this.onRemove}
                    />
                </div>

            </li >
        );
    };
}

export default ListItem;