import React from 'react';


class Form extends React.Component {
    inputValue = {
        todo: ""
        //adding Required to the Form with no Space in Todo, so no empty tasks can be submitted.
    }
    state = this.inputValue;
    handleInput = (e) => {
        const { name, value } = e.target;
        this.setState({
            [name]: value
        })
    };

    handleOnSubmit = (e) => {
        //preventing the page from refreshing everytime the submit button is clicked.
        e.preventDefault();
        //passing the new values to the form.
        this.props.onSubmit(this.state);
        //emptying the placeholder when a new value is submitted.
        this.setState(this.inputValue);


    }

    render() {
        const { todo } = this.state;
        return (
            <form onSubmit={this.handleOnSubmit}>
                <input type="text"
                    placeholder="enter task here"
                    name="todo"
                    id="todo"
                    value={todo}
                    onChange={this.handleInput}
                    className="to-do"
                    required
                />
                <button type="submit">Add task</button>
            </form>
        );
    };
}

export default Form;